.speech {
  position: relative;
  /* width: 100%; */
  /* height: 100px; */
  /* min-height: 150px; */
  padding: 12px 8px;
  background-color: #fff;
  border: 8px solid #fff;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 5px;
  -webkit-box-shadow: 6px 4px 2px #eaeaea;
  -moz-box-shadow: 6px 4px 2px #eaeaea;
  box-shadow: 6px 4px 2px #eaeaea;
}
.speech:before {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  left: 30px;
  bottom: -50px;
  border: 25px solid;
  border-color: #fff transparent transparent #fff;
}
